import SongListView from './components/SongListView.vue'
import SongDetails from './components/SongDetails.vue'
import About from './components/About.vue'

export default {
  '/': {
    name: 'SongListView',
    component: SongListView
  },
  '/about': {
    name: 'About',
    component: About
  },
  '/song/:songKey': {
    name: 'SongDetails',
    component: SongDetails
  }
}



